export default {
	data() {
		return {
			goodsVO:{},
			goodsList:[]
		};
	},
	onLoad(options) {
		
	},
	onShow(){
		this.getReceiveList()
	},
	methods: {
		getReceiveList() {
			let self = this
			let params = {
				pageIndex: 1,
				pageSize:100,
				fetchImgList:true,
				useType:''
			}
			this.$acFrame.HttpService.myReceiveGoods(params).then(res => {
				if (res.success) {
					let list = res.data.rows
					if (list.length > 0) {
						list.filter((v,i)=>{
							if(i==0){
								v.choose=true
							}else{
								v.choose=false
							}
						})
						self.goodsList = list
						self.goodsVO = list[0].virtualVo
					}
				}
			})
		},
		chooseItem(index){
			let self = this
			this.goodsList.filter((v,i)=>{
				if(i==index){
					v.choose=true
					self.goodsVO = v.virtualVo
				}else{
					v.choose=false
				}
			})
			console.log(self.goodsVO)
			
		},
		buy(){
			uni.navigateTo({
				url:'myWallet'
			})
		},
		setImg(src) {
			return this.$acFrame.Util.setImgUrl(src);
		}
	}
}
